<template>
    <v-container>
        <v-card>
            <v-container grid-list-md mb-0>
                <h2 class="text-md-center">Posting Etiket CPKB</h2>
                <v-layout row wrap style="margin:10px">
                    <v-flex xs6>
                        <!-- <v-btn depressed dark rounded style="text-transform: none !important;" class="mr-2" color="green accent-3"
                            @click="dialog = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Entry Etiket
                        </v-btn> -->
                        <v-btn depressed dark rounded style="text-transform: none !important;" color="blue accent-3"
                            @click="dialog2 = true">
                            <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>
                            Posting Etiket Belakang
                        </v-btn>
                    </v-flex>
                    <v-flex xs6 class="text-right">
                        <v-text-field v-model="keyword" append-icon="mdi-search" label="Search" hide-details>
                        </v-text-field>
                    </v-flex>
                </v-layout>
                <v-data-table :headers="headers" :items="produksis" :search="keyword" :loading="load">
                    <template v-slot:body="{ items }">
                        <tbody>
                            <tr v-for="(item,index) in items" :key='item["id"]'>
                                <td class="title">{{ index + 1 }}</td>
                                <td class="title">{{ item["Tanggal"] }}</td>
                                <td class="title">{{ item["Kode Barang"] }}</td>
                                <td class="title">{{ item["No Bet"] }}</td>
                                <td class="title">{{ item["Jumlah Barang"] }}</td>
                                <td v-if="item['CPKB'] === 1"> <v-simple-checkbox :value="true" color="success" disabled></v-simple-checkbox></td>
                                <td v-else-if="item['CPKB'] === 0"> <v-simple-checkbox disabled></v-simple-checkbox></td>
                                <!-- <td>
                                    
                                    <v-btn icon color="error" light @click='deleteData(item["id"])'>
                                        <v-icon>mdi-delete</v-icon>
                                    </v-btn>
                                </td> -->
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-container>
        </v-card>
        <v-dialog v-model="dialog2" persistent max-width="500px">
            <v-card>
                <v-card-title>
                    <span class="headline">Posting Etiket CPKB</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row no-gutters>
                            <v-col cols="4">
                                <h2 class="mt-2">No Bet</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-autocomplete dense single-line outlined :items="dataBet" item-text="No Bet" item-value="No Bet"
                                    label="No Bet" class="title" v-model="form.noBet">    
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="4">
                                <h2 class="mt-2">Pelaksana</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-autocomplete dense single-line outlined :items="pegawais" item-text="Nama" item-value="Nama"
                                    label="Nama" class="title" v-model="form.pelaksana">    
                                </v-autocomplete>
                            </v-col>
                            <v-col cols="4">
                                <h2 class="mt-2">Pengawas</h2>
                            </v-col>
                            <v-col class="ml-4" cols="7">
                                <v-autocomplete dense single-line outlined :items="pegawais" item-text="Nama" item-value="Nama"
                                    label="Nama" class="title" v-model="form.pengawas">    
                                </v-autocomplete>
                            </v-col>
                        </v-row>
                    </v-container>
                    <!-- <small>*indicates required field</small> -->
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red white--text" dense rounded @click="close">Close</v-btn>
                    <v-btn class="ml-4 white--text" dense color="blue darken-1" rounded @click="Posting()" :loading="loading">Posting</v-btn>
                    <!-- <v-btn color="blue darken-1" text @click="close">Close</v-btn>
                    <v-btn color="blue darken-1" text @click="Posting()">Save</v-btn> -->
                </v-card-actions>
            </v-card>
        </v-dialog>
        <v-snackbar v-model="snackbar" :color="color" :multi-line="true" :timeout="3000">
            {{ text }}
            <v-btn dark text @click="snackbar = false">
                Close
            </v-btn>
        </v-snackbar>
    </v-container>
</template>
<script>
    export default {
        data() {
            return {
                pegawais: [],
                form: {
                    noBet: '',
                    pelaksana: '',
                    pengawas: '',
                },
                dataBet:[],
                tanggalPost: '',
                statusUpdate: true,
                date: new Date().toISOString().substr(0, 10),
                menu: false,
                menu2: false,
                dialog2: false,
                keyword: '',
                headers: [{
                        text: 'No',
                        value: 'no',
                        class: 'title',
                    },
                    {
                        text: 'Tanggal',
                        value: 'Tanggal',
                        class: 'title',
                    },
                    {
                        text: 'Kode Barang',
                        value: 'Kode Barang',
                        class: 'title',
                    },
                    {
                        text: 'No Bet',
                        value: 'No Bet',
                        class: 'title',
                    },
                    {
                        text: 'Jumlah Barang',
                        value: 'Jumlah Barang',
                        class: 'title',
                    },
                    {
                        text: 'Posted',
                        value: 'Posted',
                        class: 'title',
                    },
                    // {
                    //     text: 'Aksi',
                    //     value: '',
                    //     class: 'title',
                    // },
                ],
                stoks: [],
                produksis: [],
                Postingoplosans: [],
                tampung: [],
                sisa: [],
                snackbar: false,
                color: null,
                text: '',
                load: false,
               
                produksi: new FormData,
                mutstok: new FormData,
                typeInput: 'new',
                errors: '',
                updatedId: '',
                totalKeluar: 0,
                tanggalPerkiraan: '',
                batasanbet: '',
                loading: false,
            }
        },
        methods: {
            async getDataPegawai() {
                var uri = this.$apiUrl + 'pegawais'
                var res = await this.$http.get(uri)
                this.pegawais = res.data
            },

            async Posting() {
                var uri = this.$apiUrl + 'produksis/cekcpkbpost/' + this.form.noBet
                var res = await this.$http.get(uri)
                var status = res.data
                if(status === 1){
                    var uri = this.$apiUrl + 'produksis/postcpkb/' + this.form.noBet + '/' + this.form.pelaksana + '/' + this.form.pengawas
                    await this.$http.post(uri)
                }else{
                    await alert("Etiket Belum Lengkap")
                }
                await this.close()
                
            },
            getDataBarang() {
                var uri = this.$apiUrl + 'tblstoks/kode/13050'
                this.$http.get(uri).then(response => {
                    this.stoks = response.data.data
                })
            },
            getData() {
                var uri = this.$apiUrl + 'produksis'
                this.$http.get(uri).then(response => {
                    this.produksis = response.data
                    this.getDataByNoBet()
                    this.getDataPegawai()
                })

            },
            Activity(key) {
                // untuk memposting aktivitas user yang login
                var activity = new FormData
                activity.append('activity', key)
                var uri = this.$apiUrl + 'activity'
                this.$http.post(uri, activity).then(response => {}).catch(error => {
                    this.errors = error
                })
            },
            sendData() {
                this.produksi.append('tanggal', this.form.tanggal);
                this.produksi.append('kodeBarang', this.form.kodeBarang);
                this.produksi.append('jumlahDosen', this.form.jumlahDosen);
                this.produksi.append('jumlahBotol', this.form.jumlahBotol);
                var uri = this.$apiUrl + 'produksis'
                this.load = true
                this.$http.post(uri, this.produksi).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackbar
                    this.load = false;
                    this.dialog = false
                    this.getData(); //mengambil data user
                    this.resetForm();
                    // this.Activity('Melakukan Penambahan Data Entry Oplosan');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                })
            },

            updateData() {
                this.produksi.append('tanggal', this.form.tanggal);
                this.produksi.append('kodeBarang', this.form.kodeBarang);
                this.produksi.append('jumlahDosen', this.form.jumlahDosen);
                this.produksi.append('jumlahBotol', this.form.jumlahBotol);
                var uri = this.$apiUrl + 'produksis/' + this.updatedId;
                this.load = true
                this.$http.post(uri, this.produksi).then(response => {
                    this.snackbar = true; //mengaktifkan snackbar
                    this.color = 'green'; //memberi warna snackbar
                    this.text = response.data; //memasukkan pesan ke snackba
                    this.load = false;
                    this.dialog = false
                    this.statusUpdate = true
                    this.getData(); //mengambil data user
                    this.resetForm();
                    // this.Activity('Melakukan Perubahan Entry Oplosan');
                    this.typeInput = 'new';
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                    this.load = false;
                    this.typeInput = 'new';
                })
            },
            
            editHandler(item) {
                this.typeInput = 'edit';
                this.statusUpdate = false
                this.dialog = true;
                this.form.tanggal = item["Tanggal"];
                this.form.kodeBarang = item["Kode Barang"];
                this.form.jumlahDosen = item["Jumlah Dosen"];
                this.form.jumlahBotol = item["Jumlah Botol"];
                this.updatedId = item["id"];
                console.log(this.updatedId)
            },

            deleteData(deleteId) { //mengahapus data
                var uri = this.$apiUrl + 'produksis/' + deleteId; //data dihapus berdasarkan id
                this.$http.delete(uri).then(response => {
                    this.snackbar = true;
                    this.text = response.data;
                    this.color = 'green'
                    this.deleteDialog = false;
                    this.getData();
                    // this.Activity('Melakukan Delete Entry Oplosans');
                }).catch(error => {
                    this.errors = error
                    this.snackbar = true;
                    this.text = 'Try Again';
                    this.color = 'red';
                })
            },

            getDataByNoBet(){
                var uri = this.$apiUrl + 'produksis/getbetbelumdiposting'
                this.$http.get(uri).then(response => {
                    this.dataBet = response.data
                })
                
            },

            setForm() {
                if (this.typeInput === 'new') {
                    this.sendData()
                    
                } else {
                    this.updateData()
                }
            },
            close() {
                this.dialog2 = false
                this.resetForm()
            },
            resetForm() {
                this.form= {
                    noBet: '',
                    pelaksana: '',
                    pengawas: '',
                },
                this.statusUpdate = true
                this.getData()
            }
        },
        mounted() {
            this.getData();
            this.getDataBarang();
        },
    }
</script>